import TableCell from '../../mo/models/tableCell';
import createEmptyParagraph from './createEmptyParagraph';

export default function createEmptyTableCell(data?: TableCell['data']): TableCell {
  return TableCell.create({
    data: { ...(data || {}), colSpan: 1, rowSpan: 1 },
    nodes: [createEmptyParagraph()],
  });
}
